import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';
import { LoadingProvider } from '../../providers/loading/loading';
import { AuthProvider } from '../../providers/auth/auth';

@IonicPage()
@Component({
  selector: 'page-group-members',
  templateUrl: 'group-members.html',
})
export class GroupMembersPage {
  public members = [];

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public api: ApiProvider,
    public loading: LoadingProvider,
    public auth: AuthProvider
  ) {
    this.loading.show('getGroupMembers');
    this.api.getGroupMembers({group_id: this.navParams.data.id, user_id: this.auth.user.id}).subscribe((res: any) => {
      this.members = res.data;
      console.log(this.members);
      this.loading.dismiss('getGroupMembers');
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad GroupMembersPage');
  }

}



